@import "normalizes.scss";
@import "resets.scss";

$mobile-breakpoint: 768px;
@mixin mobile {
  @media (max-width: $mobile-breakpoint) {
    @content; } }



@font-face {
  font-family: "Proxima Nova";
  src: url(../fonts/ProximaNova-Bold.ttf);
  font-weight: 700; }

@font-face {
  font-family: "Proxima Nova";
  src: url(../fonts/ProximaNova-Regular.ttf);
  font-weight: 400; }

@font-face {
  font-family: "Barlow Semi Condensed";
  src: url(../fonts/BarlowSemiCondensed-ExtraBold.ttf);
  font-weight: 800; }

@font-face {
  font-family: "Barlow Semi Condensed";
  src: url(../fonts/BarlowSemiCondensed-Bold.ttf);
  font-weight: 700; }

@font-face {
  font-family: "Barlow Semi Condensed";
  src: url(../fonts/BarlowSemiCondensed-Medium.ttf);
  font-weight: 500; }

@font-face {
  font-family: "Barlow Semi Condensed";
  src: url(../fonts/BarlowSemiCondensed-Regular.ttf);
  font-weight: 400; }

.container {
  max-width: 1140px;
  margin: 0 auto;
  @include mobile {
    max-width: 500px;
    margin: 0 auto; } }


.main-secrion {
  height: 780px;
  background: #C9C9C9;
  padding-top: 40px;
  margin-bottom: 198px;
  &__nav-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 49px;
    align-items: center; } }

.main-secrion__logo {
  @include mobile {
    width: 80%; } }

.nav-menu {
  &__list {
    display: flex;
    flex-direction: row;
    &-item {
      font-size: 25px;
      font-family: 'Proxima Nova';
      font-weight: 700;
      @include mobile {
        font-size: 18px; } } } }


.central-background {
  height: 380px;
  margin-bottom: 200px;
  margin-top: 150px;
  @include mobile {
    margin-top: 100px;
    height: 440px; } }



.central-background__img {
  margin-left: 30px;
  max-width: 96%;
  height: 380px;
  @include mobile {
    max-width: 260%;
    margin-left: 15px; } }



.main-section__central-container {
